import { Injectable } from '@angular/core';

import { Observable, combineLatest } from 'rxjs';
import { map } from 'rxjs/operators';

import { CompanyEntityHelperService } from './company-entity-helper.service';
import { CurrentCompanyEntityCollectionService } from './current-company-entity-collection-service';
import { HouseholdFilers } from '@model';
import { SessionService } from '@session';

@Injectable({providedIn: 'root'})
export class HouseholdFilersService extends CurrentCompanyEntityCollectionService<HouseholdFilers> {
  constructor(
    protected helper: CompanyEntityHelperService,
    protected sessionService: SessionService
  ) {
    super('HouseholdFilers', helper);
  }

  get householdFilers$(): Observable<HouseholdFilers> {
    return combineLatest(
      this.entities$,
      this.sessionService.currentCompanyId$
    ).pipe(
      map( ([filersCollection, companyId]) => {
        return filersCollection.find(f => f.companyId === companyId) ||
          this.createNewRecord(companyId);  // not found, create one
      })
    );
  }

  private createNewRecord(companyId: string): HouseholdFilers {
    if (!companyId) {
      return null;
    }
    const res = new HouseholdFilers(null, companyId);
    return res;
  }
}
