import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { Observable } from 'rxjs';

import { CompanyEntityHelperService } from './company-entity-helper.service';
import { CurrentCompanyEntityCollectionService } from './current-company-entity-collection-service';
import { Document } from '@model';
import { environment } from '@environment';
import { BusyService } from '@app/core';

@Injectable({providedIn: 'root'})
export class DocumentService extends CurrentCompanyEntityCollectionService<Document> {
  constructor(
    protected helper: CompanyEntityHelperService,
    private http: HttpClient,
    private busyService: BusyService
  ) {
    super('Document', helper);
  }

  /**
   * Get the PDF for a signable document by its id.
   * @param docId Id of the PDF to download
   * @returns The PDF as a Blob
   */
  getDocPdf(docId: string): Observable<Blob> {
    // Use HttpClient to download (vs just a link) because requires Bearer token
    const url = environment.apiRoot + `/document/download/${docId}`;
    return this.busyService.busy$('Building Document ...',
      this.http.get<Blob>(url, { responseType: 'blob' as 'json' })
    );
  }
}
