import { Component, OnDestroy, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { Router } from '@angular/router';
import { CompanyService } from '@entity';
import { DocumentPackageService } from '@entity/entity-services/document-package.service';
import { BusyService, SubSink, WellKnownRoutes } from '@core';
import { AccountStatus, Document } from '@model';
import { Observable, timer } from 'rxjs';
import { concatMap, first, map } from 'rxjs/operators';
import { AncillaryModalComponent } from '@app/ancillary/ancillary-modal.component';

/** Time (in ms) between polling for status change */
const pollDelay = 5000;
@Component({
  selector: 'sp-processing',
  templateUrl: './processing.component.html'
})
export class ProcessingComponent implements OnInit, OnDestroy, AfterViewInit {
  private companyId: string;
  /** id of first completed, non-removed envelope with an embedded envelope signer (if exists) */
  signedEnvelopeId$: Observable<string>;
  private subs = new SubSink();
  migrationStopped = false;
  @ViewChild(AncillaryModalComponent)
  ancillaryModal: AncillaryModalComponent;
  wetdocs$: Observable<Document[]>;

  constructor(
    private companyService: CompanyService,
    private documentPackageService: DocumentPackageService,
    private router: Router,
    private busyService: BusyService
  ) { }

  ngOnInit() {
    // id of the first completed, non-removed envelope with an embedded envelope signer (if exists)
    const signedEnvelopeId$ = this.documentPackageService.loadSignedEnvelopeId();

    // If enrolled, leave immediately
    // if not enrolled, define the signedEnvelopeId$ and let the template subscribe to it.
    this.companyService.currentCompany$.pipe(first()).subscribe(
      company => {
        this.companyId = company.id;
        // Leave this page immediately if enrolled.
        if (company.status === AccountStatus.Enrolled) {
          this.router.navigate([WellKnownRoutes.Enrolled]);
        } else {
          if (company.status === AccountStatus.Operations) {
            this.migrationStopped = true;
          }
          this.signedEnvelopeId$ = signedEnvelopeId$;
        }
      });

    if (!this.migrationStopped) {
      this.startPolling();
    }

    this.wetdocs$ = this.documentPackageService.documentPackage$.pipe(
      map(dp => {
        const wetenv = dp.Envelope.find(e => e.isWetSign);
        return wetenv ? dp.Document.filter(d => d.envelopeId === wetenv.id) : [];
      })
    );

  }
  ngAfterViewInit() {
      setTimeout(() => this.ancillaryModal.checkAncillaryResponse());
  }
  private startPolling() {
    this.busyService.increment('Processing...');
    // poll the server to get status updates
    this.subs.sink = timer(5000, pollDelay).pipe(
      // reload the company to get the latest status and trigger currentCompany$ update
      concatMap(tick => this.companyService.getByKey(this.companyId))
    ).subscribe(
      company =>  {
        // Leave this page immediately if status becomes enrolled.
        if (company && company.status === AccountStatus.Enrolled) {
          this.busyService.decrement();
          this.router.navigate([WellKnownRoutes.Enrolled]);
        } else if (company && company.status === AccountStatus.Operations) {
          this.busyService.decrement();
          this.migrationStopped = true;
          this.subs.unsubscribe();
        }
      }
    );

  }

  ngOnDestroy() {
    this.busyService.decrement();
    this.subs.unsubscribe();
  }
}
